import mainSliderComponent from '../mainSlider-component.vue'

import sliderImages1 from '../../../assets/images/Medical Tourism/1.jpg'
import sliderImages2 from '../../../assets/images/Medical Tourism/2.jpg'
import sliderImages3 from '../../../assets/images/Medical Tourism/1.jpg'
import sliderImages4 from '../../../assets/images/Medical Tourism/3.jpg'

export default {
  components: {
    mainSliderComponent,
    },
    data() {
        return {
          sliderImages: {sliderImages1, sliderImages2, sliderImages3, sliderImages4},
           form: {
          email: '',
          name: '',
          country: '',
          phone: '',
          message: '',
        },
        show: true
        };
    },
    methods: {
      showModal() {
        this.$refs['my-dialog'].show()
      },
      hideModal() {
        this.$refs['my-dialog'].hide()
      },
      onSubmit(event) {
        event.preventDefault()
        alert(JSON.stringify(this.form))
        this.onReset()
      },
      onReset(event) {
        event.preventDefault()
        // Reset our form values
        this.form.email = ''
        this.form.name = ''
        this.form.message = ''
        this.form.phone = ''
        // Trick to reset/clear native browser form validation state
        this.show = false
        this.$nextTick(() => {
          this.show = true
        })
      }
    }
}
